*{margin: 0; padding: 0;}

div.char-list {padding-bottom: 80px;}
div.char-list > ul {width: 100%; list-style: none;}
div.char-list > ul li {width: 100%;}


/** chat list의 기본적인 css
left : 본인이 쓴 경우
right : 상대방이 쓴 경우
center : 방에 입장에 했을 경우 */
div.char-list > ul li.left {text-align: left;}
div.char-list > ul li.right {text-align: right;}
div.char-list > ul li.center {
    text-align: center; border: 1px solid #007AAE; background: aliceblue;
    border-top-right-radius: 5px;border-bottom-right-radius: 5px;border-top-left-radius: 5px;border-bottom-left-radius: 5px;
}

div.char-list > ul li > div { font-size : 13px;}
div.char-list > ul li > div.sender { margin: 10px 20px 0 20px; font-weight: 400; }
div.char-list > ul li > div.message { 
    display: inline-block; word-break:break-all; margin: 5px 20px; max-width: 75%; border: 1px solid #888; 
    padding: 10px; border-radius: 5px; background-color: #FCFCFC; color: #555; text-align: left;}

div.char-inpit div.char-textarea {border-top: 1px solid #F18C7E;}

/*
resize -> tag의 크기 조절을 막아준다.
:focus -> input, textarea가 focus가 될 때의 CSS를 잡아준다 
*/
div.char-input > div.char-textarea.none {visibility: hidden; height: 0px;}
div.char-input > div.char-textarea.on {visibility: visible;}
div.char-input > div.char-textarea > textarea {width: 100%; height: 80px; border: none; padding: 10px; font-size:10px; resize: none;}
div.char-input div.char-textarea > textarea:focus{outline:none};

div.char-input > div.none.here {visibility: visible;}
div.char-input > div.on.here {visibility: hidden; height: 10px;}

#toast {position: fixed;bottom: 30px;left: 50%;padding: 15px 20px;transform: translate(-50%, 10px);border-radius: 30px;overflow: hidden;font-size: .8rem;opacity: 0;visibility: hidden;transition: opacity .5s, visibility .5s, transform .5s;background: rgba(0, 0, 0, .35);color: #fff;z-index: 10000;}

#toast.on {opacity: 1;visibility: visible;transform: translate(-50%, 0);}